<style type="text/css">
.ll-fontw{
  font-weight: bold;
  font-size: 17px;
}
.ll-p-m-w {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.ll-img-w {
  width: 60px;
  height: 60px;
}
.ll-img-w>img {
  width: 60px;
}
.ll-p-m-c-r {
  padding: 0 10px;
}
.ll-erp-p-no {
  color: #666;
  font-size: 12px;
}
.ll-sort-num .el-button--mini{
  padding: 6px 5px;
}
</style>
<template>
  <div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <p class="ll-fontw">{{cityName + '分组商品管理 - ' + groupMsg.name}}</p>
      </div>
      <hr class="s-slip">
      <div class="handel-wraper">
        <el-button type="primary"  size="mini" icon="el-icon-plus" @click="showPreList">导入预售商品</el-button>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <el-form-item label="ERP编码">
            <el-input class="search-input" v-model.trim="searchContent.productNo" placeholder="请输入商品ERP编码" size="small" @keyup.enter.native="doSearchHandel" clearable></el-input>
          </el-form-item>
          <el-form-item label="商品名称">
            <el-input class="search-input" v-model.trim="searchContent.productName" placeholder="请输入名称关键字" size="small" @keyup.enter.native="doSearchHandel" clearable></el-input>
          </el-form-item>
          <el-form-item label="状态">
            <el-select class="search-input" v-model="searchContent.productStatus" placeholder="请选择状态" size="small" @change="doSearchHandel" clearable>
              <el-option
                v-for="item in statusOptions"
                :key="item.code"
                :label="item.msg"
                :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <!-- 搜索结果 -->
    <div class="table-wraper">
      <el-table
        class="fixed-height"
        :data="tableData"
        v-loading="loading"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column min-width="240" label="商品信息">
          <template slot-scope="scope">
            <div class="ll-p-m-w">
              <div class="ll-img-w">
                <img v-if="scope.row.productImg && scope.row.productImg != ''" :src="scope.row.productImg" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                <img v-else src="/shop/static/img_error.png" alt="">
              </div>
              <div class="ll-p-m-c-r">
                <div>{{scope.row.productName}}</div>
                <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="80" prop="spec" label="规格"></el-table-column>
        <el-table-column min-width="80" prop="presalePrice" label="预售价"></el-table-column>
        <el-table-column width="185" label="预售时间" :formatter="timeFormatter"></el-table-column>
        <el-table-column min-width="80" :formatter="statusFormatter" label="状态"></el-table-column>
        <el-table-column min-width="120" prop="sortNum" label="排序">
          <template slot-scope="scope">
            <div v-if="scope.row.showUpdate == false" class="ll-sort-num">
              <el-button-group>
                <el-input placeholder="请输入" v-model="scope.row.sortNum" size="mini" style="width: 80px;" @focus="modifySortNum(scope.$index, scope.row)"></el-input>
              </el-button-group>
            </div>
            <div v-else-if="scope.row.showUpdate == true" class="ll-sort-num">
              <el-button-group>
                <el-input placeholder="请输入" v-model="scope.row.updateSortNum" size="mini" style="width: 80px;"  @blur="deleteSortNum(scope.$index, true)"></el-input>
              </el-button-group>
              <el-button-group>
                <el-button icon="el-icon-check" size="mini" style="width: 25px;" @click="sureSortNum(scope.row)"></el-button>
                <el-button icon="el-icon-close" size="mini" style="width: 25px;" @click="deleteSortNum(scope.$index)"></el-button>
              </el-button-group>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="80" label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="removeProduct(scope.row)">移除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页条 -->
      <el-pagination
        v-show="tableData.length>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="currentPage"
        :page-sizes="[20, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <!-- 选择预售商品dialog -->
    <el-dialog title="选择商品" :visible.sync="isShowPreList" width="700px">
      <pre-list ref="PreListDialog" @closeDialog="closeDialog"></pre-list>
    </el-dialog>

  </div>
</template>

<script>
import Storage from '@/utils/localStorage'
import PreList from './choosePreProducts'
import * as utils from '@/utils/utils'
 
const mgGroupProductsUrl = {
  // 获取分组状态列表
  getGroupProductPage: '/plateform/a/mall/groupProduct/getGroupProductPage',
  // 获取分组商品列表
  getGroupProductList: '/plateform/a/mall/groupProduct/getGroupProductList',
  // 保存分组商品(添加)
  saveGroupProduct: '/plateform/a/mall/groupProduct/saveGroupProduct',
  // 排序/删除分组商品
  updateGroupProduct: '/plateform/a/mall/groupProduct/updateGroupProduct'

}
export default {
  data() {
    return {
      loading: false,
      isShowPreList: false,
      currentPage: 1,
      pageSize: 20,
      total: 0,
      tableData: [],
      statusOptions: [], // 城市列表
      cityName: '', // 搜索城市名
      groupMsg: [], // 分组信息
      searchContent: { //搜索条件
        groupId: '', // 分组ID
        productNo: '', // ERP编码
        productName: '', // 商品名称
        productStatus: '' // 状态
      },
      choosePreList: [] // 预售商品
    }
  },
  created() {
    this.cityName = Storage.get('groupProductsCityName')
    this.groupMsg = Storage.get('groupProducts')
    this.searchContent.groupId = this.groupMsg.id
    // 获取状态列表
    this.getStatusList()
    // 搜索
    this.doSearchHandel()
  },
  mounted() {
  },
  components: {
    'pre-list': PreList
  },
  methods: {
    /**
     * [timeFormatter 时间格式化]
     * @return {[type]} [description]
     */
    timeFormatter(row, column, cellValue) {
      if(row.preStartDate && row.preEndDate && row.preStartTime && row.preEndTime) {
        let value = row.preStartDate + '-' + row.preEndDate + ' ' + row.preStartTime + '-' + row.preEndTime
        return cellValue = value
      }else{
        return cellValue = ''
      }
    },
    /**
     * [productStatusFormatter 状态格式化]
     * @return {[type]} [description]
     */
    statusFormatter(row, column, cellValue) {
      if(row.productStatus == 0) {
        return cellValue = '已失效'
      }else if(row.productStatus == 1) {
        return cellValue = '未开始'
      }else if(row.productStatus == 2) {
        return cellValue = '进行中'
      }else if(row.productStatus == 3) {
        return cellValue = '售罄'
      }
    },
    /**
     * [changeSize description]
     * @param    {[type]}                 value [description]
     * @return   {[type]}                       [description]
     */
    changeSize (value) {
      this.pageSize = value
      this.doSearchHandel()
    },
    /**
     * [changeCurrent 点击分页条]
     * @param    {[type]}                 value [当前页]
     * @return   {[type]}                       [description]
     */
    changeCurrent (value) {
      this.currentPage = value
      this.doSearchHandel()
    },
    /**
     * [getStatusList 获取状态列表]
     * @return {[type]} [description]
     */
    getStatusList() {
      this.$http.post(mgGroupProductsUrl.getGroupProductPage, {groupId: this.searchContent.groupId}).then(res => {
        if(res.code === 0) {
         if(res.data && res.data.productStatusList && res.data.productStatusList.length) {
            this.statusOptions = res.data.productStatusList.filter((item, i) => {
              if(item.code != null) {
                return item
              }
            })
          }else {
            this.statusOptions = []
          }
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        console.log("提示(获取状态列表):", e.message)
      })
    },
    /**
     * [doSearchHandel 搜索]
     * @return {[type]} [description]
     */
    doSearchHandel() {
      this.loading = true
      this.$http.post(mgGroupProductsUrl.getGroupProductList, { ...this.searchContent, pageNum: this.currentPage, pageSize: this.pageSize}).then(res => {
        this.loading = false
        if(res.code === 0) {
          if(res.data && res.data.result && res.data.result.length) {
            let tableData = res.data.result
            tableData.forEach((item, i) => {
              item['updateSortNum'] = item['sortNum']
              item['showUpdate'] = false
            })
            this.tableData = tableData
            this.choosePreList = []

          }else {
            this.tableData = []
          }

          if(this.currentPage == 1) {
            this.total = res.data.total
          }
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [removeProduct 移除商品]
     * @return {[type]} [description]
     */
    removeProduct(val) {
      this.loading = true
      this.$http.post(mgGroupProductsUrl.updateGroupProduct, {isDeleted: 1, groupId: this.searchContent.groupId, presaleId: val.presaleId}).then(res => {
        this.loading = false
        if(res.code === 0) {
          this.$message({
            message: '移除商品成功',
            type: 'success'
          })
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [showPreList 显示预售商品列表]
     * @return {[type]} [description]
     */
    showPreList() {
      let _this = this
      let tableData = []
      this.isShowPreList = true
      setTimeout(function() {
        // 调用子组件的方法
        _this.$refs.PreListDialog.setDefault(_this.groupMsg, tableData)
      }, 200)
    },
    /**
     * [closeDialog 导入预售商品]
     * @return {[type]} [description]
     */
    closeDialog(val) {
      this.isShowPreList = val.isShowGoods;
      if(val.choosePreList) {
        let choosePreList = val.choosePreList

        let presaleIds = choosePreList.map((item, i) => {
          return item.presaleId
        })
        this.loading = true
        this.$http.post(mgGroupProductsUrl.saveGroupProduct, {groupId: this.searchContent.groupId, presaleIds: presaleIds}).then(res => {
          this.loading = false
          if(res.code === 0) {
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }
    },
    /**
     * [modifySortNum 输入排序]
     * @return {[type]} [description]
     */
    modifySortNum(index, val) {
      if(val.showUpdate == true) {
        return
      }
      this.tableData[index].showUpdate = true
    },
    /**
     * [deleteSortNum 删除排序]
     * @return {[type]} [description]
     */
    deleteSortNum(index, val) {
      if(val){
        setTimeout(() => {
          this.tableData[index].updateSortNum = this.tableData[index].SortNum
          this.tableData[index].showUpdate = false
        }, 200)
      }else{
        this.tableData[index].updateSortNum = this.tableData[index].SortNum
        this.tableData[index].showUpdate = false
      }
    },
    /**
     * [sureSortNum 确认排序]
     * @return {[type]} [description]
     */
    sureSortNum(val) {
      let reg = /^[0-9]*$/

      if(val.updateSortNum == null || !reg.test(val.updateSortNum) == true) {
        this.$message("请输入正确的排序")
        return
      }

      if(val.updateSortNum <= 0 || val.updateSortNum > 99) {
        this.$message("排序应大于0，小于等于99")
        return
      }

      this.loading = true
      this.$http.post(mgGroupProductsUrl.updateGroupProduct, {SortNum: val.updateSortNum, groupId: this.searchContent.groupId, presaleId: val.presaleId}).then(res => {
        this.loading = false
        if(res.code === 0) {
          this.$message({
            message: '排序成功',
            type: 'success'
          })
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    }

  }

}
</script>


