<style type="text/css">
.ll-p-m-w {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.ll-img-w {
  width: 60px;
  height: 60px;
}
.ll-img-w>img {
  width: 60px;
}
.ll-p-m-c-r {
  padding: 0 10px;
}
.ll-erp-p-no {
  color: #666;
  font-size: 12px;
}
</style>
<template>
  <div>
    <!-- 操作框 -->
    
    <el-row>
      <el-input class="search-input" v-model.trim="searchContent.productNo" placeholder="请输入商品ERP编码" size="small" @keyup.enter.native="doSearchHandel" clearable></el-input>
      <el-input class="search-input" v-model.trim="searchContent.productName" placeholder="请输入名称关键字" size="small" @keyup.enter.native="doSearchHandel" clearable></el-input>
      <el-cascader class="search-input" expand-trigger="click" :options="categoriesList" placeholder="请选择分类" change-on-select v-model="categoriesSelectList" size="small" clearable></el-cascader>
      <el-button type="primary" @click="doSearchHandel" size="mini">搜索</el-button>
    </el-row>

    <!-- 搜索结果 -->
    <el-row>
      <el-table
        class="fixed-height"
        :data="tableData"
        v-loading="loading"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        ref="multipleTable"
        max-height="320"
        @select="handleSelect"
        @select-all="handleSelectAll"
        size="small">
        <el-table-column type="selection"></el-table-column>
        <el-table-column min-width="180" label="商品信息">
          <template slot-scope="scope">
            <div class="ll-p-m-w">
              <div class="ll-img-w">
                <img v-if="scope.row.productImg && scope.row.productImg != ''" :src="scope.row.productImg" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                <img v-else src="/shop/static/img_error.png" alt="">
              </div>
              <div class="ll-p-m-c-r">
                <div>{{scope.row.productName}}</div>
                <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="60" prop="presalePrice" label="预售价"></el-table-column>
        <el-table-column width="185" label="预售时间" :formatter="timeFormatter"></el-table-column>
        <el-table-column min-width="80" prop="productStatus"  :formatter="statusFormatter" label="有效状态"></el-table-column>
      </el-table>

      <!-- 分页条 -->
      <el-pagination
        v-show="tableData.length>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="currentPage"
        :page-sizes="[10, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :pager-count="5"
        :total="total">
      </el-pagination>
    </el-row>

    <el-row style="margin:20px;">
      <el-button type="primary" @click="close(true)" size="mini" style="float: right;margin-left: 20px;">选择并导入</el-button>
      <el-button @click="close(false)" size="mini" style="float: right;">关闭</el-button>
    </el-row>

  </div>
</template>

<script>
import Storage from '@/utils/localStorage'
import * as utils from '@/utils/utils'
 
const preProductsUrl = {
  // 获取分类
  getCategoryTree: '/plateform/a/category/getCategoryTree',
  // 查询预售商品列表
  getPreProduct: '/plateform/a/mall/groupProduct/getPreProduct'
}
export default {
  data() {
    return {
      loading: false,
      currentPage: 1,
      pageSize: 10,
      total: 0,
      categoriesList: [], // 获取分类
      choosePreList: [], // 选择的商品列表
      searchContent: {
        categoryCode: [], // 分类
        productNo: '',
        productName: ''
      },
      groupMsg: [],
      tableData: [],
      categoriesSelectList: [], // 选择分类
      presaleIds: []
    }
  },
  watch: {
    categoriesSelectList(curVal, oldVal) {
      this.searchContent.categoryCode = curVal
      this.doSearchHandel()
    }
  },
  created() {
    // 获取分类
    this.getCategoriesList()
  },
  mounted() {
  },
  components: {},
  methods: {
    /**
     * [setDefault 设置默认]
     */
    setDefault(groupMsg, _val) {
      this.groupMsg = groupMsg
      this.currentPage = 1
      // 搜索
      this.doSearchHandel()

    },
    /**
     * [timeFormatter 时间格式化]
     * @return {[type]} [description]
     */
    timeFormatter(row, column, cellValue) {
      if(row.preStartDate && row.preEndDate && row.preStartTime && row.preEndTime) {
        let value = row.preStartDate + '-' + row.preEndDate + ' ' + row.preStartTime + '-' + row.preEndTime
        return cellValue = value
      }else{
        return cellValue = ''
      }
    },
    /**
     * [productStatusFormatter 状态格式化]
     * @return {[type]} [description]
     */
    statusFormatter(row, column, cellValue) {
      if(row.productStatus == 0) {
        return cellValue = '已失效'
      }else if(row.productStatus == 1) {
        return cellValue = '未开始'
      }else if(row.productStatus == 2) {
        return cellValue = '进行中'
      }else if(row.productStatus == 3) {
        return cellValue = '售罄'
      }
    },
    /**
     * [getCategoriesList 获取分类]
     * @return {[type]} [description]
     */
    getCategoriesList() {
      this.$http.post(preProductsUrl.getCategoryTree).then(res => {
        if(res.code === 0) {
          if(res.data && res.data.length) {
            this.categoriesList = res.data.map(item => {
              let label = item.categoryName
              let value = item.categoryId
              let children = []
              if(item.categoryVoList && item.categoryVoList.length) {
                let childList = item.categoryVoList
                children = childList.map(item => {
                  // let label = item.categoryId + ' ' + item.categoryName
                  let label = item.categoryName
                  let value = item.categoryId
                  return {
                    label: label,
                    value: value
                  }
                })
                return {
                  label: label,
                  value: value,
                  children: children
                }
              }else{
                return {
                  label: label,
                  value: value
                }
              }

            })
          }else {
            this.categoriesList = []

          }
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        console.log("提示(获取分类列表):", e.message)
      })
    },
    /**
     * [changeSize description]
     * @param    {[type]}                 value [description]
     * @return   {[type]}                       [description]
     */
    changeSize (value) {
      this.pageSize = value
      this.currentPage = 1
      this.doSearchHandel()
    },
    /**
     * [changeCurrent 点击分页条]
     * @param    {[type]}                 value [当前页]
     * @return   {[type]}                       [description]
     */
    changeCurrent (value) {
      this.currentPage = value
      this.doSearchHandel()
    },
    /**
     * [doSearchHandel 搜索]
     * @return {[type]} [description]
     */
    doSearchHandel() {
      this.loading = true
      this.$http.post(preProductsUrl.getPreProduct, { ...this.searchContent, pageNum: this.currentPage, pageSize: this.pageSize, queryCode: this.groupMsg.id, queryType:  1}).then(res => {
        this.loading = false
        if(res.code === 0) {
          if(res.data && res.data.result && res.data.result.length) {
            this.tableData = res.data.result
            // 设置默认值
            // this.defauleSelectFun()
          }else {
            this.tableData = []
          }

          if(this.currentPage == 1) {
            this.total = res.data.total
          }
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [defauleSelectFun 设置表格中的默认选中项]
     * @param    {[type]}                 row [description]
     * @return   {[type]}                     [description]
     */
    // defauleSelectFun() {
    //   let _this = this
    //   let rows = []
    //   for(let i of this.tableData) {
    //     let presaleId = i.presaleId
    //     this.choosePreList.forEach((item, i) => {
    //       if(item.indexOf('"presaleId":' + presaleId) > -1) {
    //         rows.push(i)
    //       }
    //     })           
    //   }
    //   if (rows.length > 0) {
    //     setTimeout(function() {
    //       rows.forEach(rowItem => {
    //         _this.$refs.multipleTable.toggleRowSelection(_this.tableData[rowItem])
    //       });
    //     }, 100)
    //   } else {
    //     this.$refs.multipleTable.clearSelection()
    //   }
    // },
    /**
     * [handleSelect 选择某一个]
     * @return {[type]} [description]
     */
    handleSelect(val, row) {
      // 临时缓存使用
      let preProduct =  JSON.stringify(row)
      let presaleId = row.presaleId
      if(this.choosePreList.length > 0) {
        let pushOne = true
        this.choosePreList.forEach((item, i) => {
          if(item.indexOf('"presaleId":' + presaleId) > -1) {
            pushOne = false
            this.choosePreList.splice(i, 1)
          }
        })
        if(pushOne) {
          this.choosePreList.push(preProduct)
        }
      }else{
        this.choosePreList.push(preProduct)
      }
    },
    /**
     * [handleSelectAll 选择多个]
     * @return {[type]} [description]
     */
    handleSelectAll(val) {
      if(val.length > 0) {        
        // 全选
        for(let item of val) {
          let preProduct =  JSON.stringify(item)
          this.choosePreList.push(preProduct)
        }
        // 去重
        this.choosePreList = Array.from(new Set(this.choosePreList))
      }else { 
        // 全不选 此时val.length<=0
        // 1.获取当前table中每一条的id
        let choosePreList = []
        for(let i of this.tableData) {
          let product =  JSON.stringify(i)
          choosePreList.push(product)
        }
        // 2.从要传递的参数中去掉这些id
        this.choosePreList = Array.from(new Set([...this.choosePreList].filter(j => !new Set(choosePreList).has(j))))
      }
      // console.log('choosePreList1', this.choosePreList)
    },
    /**
     * [close 关闭]
     * @return {[type]} [description]
     */
    close(_param) {
      if(_param) {
        // 选择并关闭
        if(this.choosePreList.length == 0) {
            this.$notify({
              title: '警告',
              message: '您还没有选择任何商品',
              type: 'warning'
            });
            return;
        }else{
          let choosePreList = this.choosePreList.map((item, i) => {
            return JSON.parse(item)
          })
          let dataMesg = {
            isShowGoods: false,
            choosePreList: choosePreList
          }
          this.choosePreList = []
          this.$refs.multipleTable.clearSelection()
          this.$emit("closeDialog", dataMesg)
        }
      }else{
        let dataMesg = {
          isShowGoods: false
        }
        this.choosePreList = []
        this.$refs.multipleTable.clearSelection()
        this.$emit("closeDialog", dataMesg)

      }

    }

  }

}
</script>


